import React, {Component} from 'react';
import Particles from "react-tsparticles";


class ParticlesContainer extends Component {
render() {
    return ( 
        <Particles
            canvasClassName="example" 
            params={{
              "particles": {
                       "number": {
                         "value": 130,
                         "density": {
                           "enable": true,
                           "value_area": 800
                         }
                       },
                       "color": {
                         "value": "#ffffff"
                       },
                       "shape": {
                         "type": "circle",
                         "stroke": {
                           "width": 3,
                           "color": "#000000"
                         },
                         "polygon": {
                           "nb_sides": 5
                         },
                         "image": {
                           "src": "img/github.svg",
                           "width": 100,
                           "height": 100
                         }
                       },
                       "opacity": {
                         "value": 0.5,
                         "random": true,
                         "anim": {
                           "enable": false,
                           "speed": 1,
                           "opacity_min": 0.1,
                           "sync": false
                         }
                       },
                       "size": {
                         "value": 4,
                         "random": true,
                         "anim": {
                           "enable": false,
                           "speed": 40,
                           "size_min": 0.1,
                           "sync": false
                         }
                       },
                       "line_linked": {
                         "enable": true,
                         "distance": 150,
                         "color": "#ffffff",
                         "opacity": 0.4,
                         "width": 1
                       },
                       "move": {
                         "enable": true,
                         "speed": 2,
                         "direction": "none",
                         "random": false,
                         "straight": false,
                         "out_mode": "out",
                         "bounce": true,
                         "attract": {
                           "enable": false,
                           "rotateX": 600,
                           "rotateY": 1200
                         }
                       }
                     },
                     "interactivity": {
                       "detect_on": "window",
                       "events": {
                         "onhover": {
                           "enable": true,
                           "mode": "grab"
                         },
                         "onclick": {
                           "enable": true,
                           "mode": "push"
                         },
                         "resize": true
                       },
                       "modes": {
                         "grab": {
                           "distance": 150,
                           "line_linked": {
                             "opacity": 1
                           }
                         },
                         "bubble": {
                           "distance": 400,
                           "size": 40,
                           "duration": 2,
                           "opacity": 8,
                           "speed": 3
                         },
                         "repulse": {
                           "distance": 200,
                           "duration": 0.4
                         },
                         "push": {
                           "particles_nb": 4
                         },
                         "remove": {
                           "particles_nb": 2
                         }
                       }
                     },
                     "retina_detect": true
            }} />
    )
}
}

export default ParticlesContainer;




